import { useLocation } from "react-router-dom"
import { HtmlPageHead } from "@rp/react/web/common/header-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { DesktopLoginContainerContents } from "./DesktopLoginContainerContents"
import { MobileLoginContainerContents } from "./MobileLoginContainerContents"

type Props = {
  children: React.ReactNode
  pageTitle: string
  title: string
  hidePolicies?: boolean
  hideCloseIcon?: boolean
}

export const LoginContainer = ({
  children,
  pageTitle,
  title,
  hidePolicies,
  hideCloseIcon,
}: Props) => {
  const { isMobile } = useDeviceInfo()
  const { pathname } = useLocation()
  return (
    <>
      <HtmlPageHead title={pageTitle} />
      {isMobile ? (
        <MobileLoginContainerContents
          children={children}
          pageTitle={pageTitle}
          title={title}
          pathname={pathname}
          hidePolicies={hidePolicies}
        />
      ) : (
        <DesktopLoginContainerContents
          children={children}
          pageTitle={pageTitle}
          title={title}
          pathname={pathname}
          hideCloseIcon={hideCloseIcon}
        />
      )}
    </>
  )
}
